@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
@import url('https://fonts.googleapis.com/css2?family=Tilt+Warp&display=swap');

#containerBanner {
    height: 85vh;
    display: grid;
    justify-content: center;
    align-items: center;
}

#containerBanner h1 {
    margin-bottom: 2%;
    font-size: clamp(1.5rem, 13.3vw - 4.3rem, 4rem);
    font-family: sans-serif;
    color: hsl(0, 0%, 100%);
    border: 4px solid black;
    padding: 5px;
    text-transform: uppercase;
    font-family: 'Tilt Warp', cursive;
}

#containerBanner p {
    position: absolute;
    font-size: clamp(0.8rem, 1.1vw + 0.3rem, 1rem);
    text-align: left;
    color: hsl(0, 0%, 100%);
    font-weight: bold;
    font-family: "Anonymous Pro", monospace;
    letter-spacing: 7px;
    overflow: hidden;
    border-right: 2px solid hsl(0, 0%, 80%);
    white-space: nowrap;
    animation: typewriter 4s steps(44) 1s 1 normal both, blinkTextCursor 500ms infinite;
}

@keyframes typewriter {
    from {
        width: 0;
    }

    to {
        width: 710px;
    }
}

@keyframes blinkTextCursor {
    from {
        border-right-color: hsl(0, 0%, 80%);
    }

    to {
        border-right-color: transparent;
    }
}

/* CARTELES ADOPTADOS */
@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');

#container-adoptados h1 {
    font-family: 'Pacifico', cursive;
    font-size: clamp(1.5rem, 8vw - 2rem, 3rem);
}

@keyframes bg-color {
    0% {
        background-color: #2dc5a4;
    }

    20% {
        background-color: #1379BD;
    }

    40% {
        background-color: #2dc5a4;
    }

    60% {
        background-color: #1379BD;
    }

    80% {
        background-color: #23b0d3;
    }

    100% {
        background-color: #2dc5a4;
    }
}


@keyframes sway {
    0% {
        transform: rotate(0);
        animation-timing-function: ease-in-out;
    }

    10% {
        transform: rotate(-12deg);
        animation-timing-function: ease-in-out;
    }

    30% {
        transform: rotate(8deg);
        animation-timing-function: ease-in-out;
    }

    45% {
        transform: rotate(-4deg);
        animation-timing-function: ease-in-out;
    }

    60% {
        transform: rotate(2deg);
        animation-timing-function: ease-in-out;
    }

    70% {
        transform: rotate(-.1deg);
        animation-timing-function: ease-in-out;
    }

    75% {
        transform: rotate(.05deg);
        animation-timing-function: ease-in-out;
    }

    80% {
        transform: rotate(0);
        animation-timing-function: ease-in-out;
    }

    100% {
        transform: rotate(0);
    }
}

.cartel img {
    border-radius: 10px 10px 10px 10px;
}

.cartel {
    position: relative;
    display: inline-block;
    font-family: 'Spicy Rice', cursive;
    padding: 15px 15px;
    margin: 64px 0;
    animation: sway 5s linear infinite, bg-color 10s infinite;
    transform-origin: 50% -32px;
    /* background: #1379BD; */
    border-radius: 10px 10px 10px 10px;
    background-size: 3px 3px,
        3px 3px,
        3px 3px,
        3px 3px,
        16px 16px,
        16px 16px,
        16px 16px,
        16px 16px,
        calc(100% - 32px + 4px) 16px,
        calc(100% - 32px + 4px) 16px,
        16px calc(50% - 32px),
        16px calc(50% - 32px),
        16px calc(50% - 32px),
        16px calc(50% - 32px),
        32px 32px,
        32px 32px,
        calc(100% - 32px + 4px) calc(100% - 32px);
    background-position: 0 calc(50% - 16px),
        100% calc(50% - 16px),
        0 calc(50% + 16px),
        100% calc(50% + 16px),
        0 0,
        100% 0,
        100% 100%,
        0 100%,
        calc(16px - 1px) 0,
        calc(16px - 1px) 100%,
        0 16px,
        0 calc(100% - 16px),
        100% 16px,
        100% calc(100% - 16px),
        0 50%,
        100% 50%,
        calc(16px - 1px) 16px;
    background-repeat: no-repeat;
}

.cartel::before {
    content: '';
    height: 2px;
    width: 51%;
    position: absolute;
    top: -13px;
    left: 0;
    z-index: -1;
    transform: rotate(-15deg);
    background: linear-gradient(to right, transparent 0%, transparent 13%, #555555 15%, #5a5a5a 17%, #c0c0c0 100%);
}

.cartel::after {
    content: '';
    height: 2px;
    width: 51%;
    position: absolute;
    top: -13px;
    right: 0;
    z-index: -1;
    transform: rotate(15deg);
    background: linear-gradient(to left, transparent 0%, transparent 13%, #555555 15%, #5a5a5a 17%, #c0c0c0 100%);
}